'use strict';

Object.defineProperty(exports, "__esModule", {
    value: true
});
var viewerModule = angular.module('ViewerService', []).factory('ViewerService', viewerService).name;

function viewerService() {
    'ngInject';

    var streetMap = L.tileLayer('http://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
        maxZoom: 19,
        attribution: '&copy; <a href="http://www.openstreetmap.org/copyright">OpenStreetMap</a>'
    });
    var service = {
        map: L.map('map', {
            center: [39.73, -104.99],
            zoom: 10,
            layers: [streetMap]
        }),
        baseMaps: {
            "Base": streetMap
        },
        populateMap: populateMap
    };
    return service;

    function populateMap(data) {
        console.log('adding to map');
        var markerList = [];
        var markers = L.markerClusterGroup({
            chunkedLoading: true,
            zoomToBoundsOnClick: false
        });
        for (var entry in data) {
            var latLng = entry.split(':');
            var marker = L.marker(L.latLng(latLng[0], latLng[1]), {
                icon: L.divIcon({ className: 'queryIcon' })
            });
            markerList.push(marker);
            data[entry].forEach(function (coords) {
                var marker = L.marker(L.latLng(coords[0], coords[1]));
                markerList.push(marker);
            });
        }
        markers.addLayers(markerList);
        service.map.addLayer(markers);
    }
}

exports.default = viewerModule;


